import {
  Streamlit,
  StreamlitComponentBase,
  withStreamlitConnection,
} from "streamlit-component-lib"
import React, { ReactNode } from "react"

interface State {
  selectedIndexImage: number
}

/**
 * This is a React-based component template. The `render()` function is called
 * automatically when your component should be re-rendered.
 */
class StreamlitClickAbleImages extends StreamlitComponentBase<State> {

  public state = {selectedIndexImage: -1}

  public render = (): ReactNode => {
    // Arguments that are passed to the plugin in Python are accessible
    // via `this.props.args`. Here, we access the "name" arg.
    const paths = this.props.args["paths"]
    const titles = this.props.args["titles"]
    const divStyle = this.props.args["div_style"]
    const imgStyle= this.props.args["img_style"]
    const key = this.props.args["key"]
    const defaultValue = this.props.args["default"]
    const backgroundColor = this.props.args["background_color"]

    const imageElements = paths.map((path: string, index: number) => (
      <img
        key={index}
        src={path}
        title={titles[index]}
        style={index === this.state.selectedIndexImage ?{ ...imgStyle, backgroundColor:backgroundColor}:imgStyle}
        onClick={() => this.onImageClick(index)}
      />
    ))
    // Return a container div with image elements
    return (
      <div key = {key} style={divStyle}>
        {imageElements}
      </div>
    )  
  }
  private onImageClick = (index: number): void => {
    // Increment state.numClicks, and pass the new value back to
    // Streamlit via `Streamlit.setComponentValue`.
    if (index === this.state.selectedIndexImage){
      this.state.selectedIndexImage = -1
      Streamlit.setComponentValue(this.state.selectedIndexImage)
    } else{
      this.state.selectedIndexImage = index
      Streamlit.setComponentValue(this.state.selectedIndexImage)
    }
  }
}

// "withStreamlitConnection" is a wrapper function. It bootstraps the
// connection between your component and the Streamlit app, and handles
// passing arguments from Python -> Component.
//
// You don't need to edit withStreamlitConnection (but you're welcome to!).
export default withStreamlitConnection(StreamlitClickAbleImages)
